const generateListItemWithHighlight = ({ listData, type = '' }) => {
	if (type === 'review') {
		return listData
			.map(
				(item) => `
          <div class="highlighted-item item-review">  
            <p>${item.review}</p>
            <p>- ${item.name}-</p>
          </div>
        `
			)
			.join('');
	}

	return listData
		.map(
			(item) => `
        <p class="highlighted-item">  
          ${item.name}
        </p>
      `
		)
		.join('');
};

export const cardRestoItemSkeleton = (count) => {
	let template = '';

	for (let i = 0; i < count; i += 1) {
		template += `
        <article class="post-item post-item-skeleton" tabindex="0">
          <div class="skeleton-image"></div>
          <div class="post-item__content post-item-skeleton__content">
            <div class="skeleton skeleton-city"></div>
            <div class="skeleton skeleton-name"></div>
            <div class="post-item-skeleton__content desc">
              <div class="skeleton skeleton-desc"></div>
              <div class="skeleton skeleton-desc"></div>
              <div class="skeleton skeleton-desc"></div>
              <div class="skeleton skeleton-desc"></div>
              <div class="skeleton skeleton-desc"></div>
            </div>
          </div>
        </article>
      `;
	}
	return template;
};

export const cardRestoItemTemplate = (restaurant) => `
  <article class="post-item" tabindex="0">
    <img
      class="post-item__thumbnail lazyload"
      data-src="${CONFIG.BASE_URL}/images/medium/${restaurant.pictureId}"
      onerror="this.onerror=null; this.src='./images/grey.jpg"
      alt="Gambar ${restaurant.name}"
    />
    <div class="post-item__content">
      <p class="post-item__city">${restaurant.city}</p>
      <h1 class="post-item__title">
        <a href="/#/restaurant/${restaurant.id}">
          ${restaurant.name}
        </a>
      </h1>
      <p class="post-item__description">
        ${restaurant.description}
      </p>
    </div>
  </article>
`;

export const restoDetailItemTemplate = (restaurant) => `
  <article class="detail-item" tabindex="0">
    <div class="section-wrapper-item">
      <h1 class="detail-item__title">
        ${restaurant.name}
      </h1>
      <div class="detail-item__content"> 
        <img
          class="detail-item__thumbnail lazyload"
          data-src="${CONFIG.BASE_URL}/images/medium/${restaurant.pictureId}"
          alt="Gambar ${restaurant.name}"
        />
        <div class="detail-item__info">
          <div class="info-item-wrapper">
            <h1>Rating</h1> 
            <p>${restaurant.rating}</p>
          </div>
          <div class="info-item-wrapper">
            <h1>City</h1>
            <p class="">${restaurant.city}</p>
          </div>
          <div class="info-item-wrapper">
            <h1>Address</h1>
            <p class="detail-item__address">${restaurant.address}</p>
          </div>
          <div class="info-item-wrapper">
            <h1>Categories</h1>
            <div class="highlight-wrapper">
              ${generateListItemWithHighlight({ listData: restaurant.categories })}
            </div>
          </div>
        </div>
      </div>
      <div class="detail-item__description">
      <h1>Tentang ${restaurant.name}</h1>
        <p class="post-item__description">
          ${restaurant.description}
        </p>
      </div>
    </div>
    <div class="detail-item__menus section-wrapper-item">
      <div class="detail-item__menu__food menu-item-wrapper">
        <h1>Food</h1>
        <div class="highlight-wrapper">
          ${generateListItemWithHighlight({ listData: restaurant.menus.foods })}
        </div>
      </div>
      <div class="detail-item__menu__drink menu-item-wrapper">
        <h1>Drink</h1>
        <div class="highlight-wrapper">
          ${generateListItemWithHighlight({ listData: restaurant.menus.drinks })}
        </div>
      </div>
    </div>
    <div class="detail-item__reviews review-wrapper section-wrapper-item">
      <h1>Reviews</h1>
      <div class="highlight-wrapper">
        ${generateListItemWithHighlight({ listData: restaurant.customerReviews, type: 'review' })}
      </div>
    </div>
  </article>
`;

export const createLikeButtonTemplate = () => `
  <button aria-label="like this resto" id="likeButton" class="like">
    ${generateHeartOutlineIcon()}
  </button>
`;

export const createLikedButtonTemplate = () => `
  <button aria-label="unlike this resto" id="likeButton" class="like">
    ${generateHeartFullIcon()}
  </button>
`;

